.App {
  background-image: linear-gradient(-180deg, #679597 0%, #574561 80%);
  text-align: center;
  align-content: center;
  align-items: center;
  position: absolute;
  flex-direction: column;
  display: flex;
  height: 100%;
  width: 100%;
}


.boxesContainer {
  display: flex; 
  flex: 0 1 1em;
  justify-content: center;
  margin-top: 10%;
  margin-bottom: 10%;
} 

.box {
  width: 6px;
  height: 6px;
  background: white;
  -webkit-transform-origin: 50% 50%;
  transform-origin: 50% 50%;
  margin-left: 20px;
}


.shuffleWrapper {
  display: flex;
  justify-content: center;
  max-height: 300px;
  flex: 0 1 1em;
}

.ul-shuffle {
  flex-wrap: wrap;
  justify-content: center;
  max-width: 500px;
  display: flex;
}

.ul-shuffle > li {
  display: block;
  background: white;
  margin: 5px;
  line-height: 100px;
  font-weight: bold;
  color: red;
  width: 110px;
  height: 110px;
  border-radius: 10px;
  font-size: 20px;
}

.startBtn {
  width: 110px;
  height: 110px;
  margin-left: 30px;
  line-height: 100px;
  background: aliceblue;
  cursor: pointer;
  color: black;
  font-size: 20px;
  font-weight: bold;
  border-radius: 20px;
  justify-content: center;
  flex: 0 1 1em;
  margin-top: 5%
} 

.nextLevelBtn {
  width: 110px;
  height: 110px;
  margin-left: 30px;
  line-height: 100px;
  background: aliceblue;
  cursor: pointer;
  color: black;
  font-size: 18px;
  font-weight: bold;
  border-radius: 20px;
  justify-content: center;
  flex: 0 1 1em;
}

.restartBtn {
  width: 110px;
  height: 110px;
  margin-left: 30px;
  line-height: 100px;
  background: aliceblue;
  cursor: pointer;
  color: black;
  font-size: 18px;
  font-weight: bold;
  border-radius: 20px;
  justify-content: center;
  flex: 0 1 1em;
}

.score{
  width: 15vw;
  height: 5vh;
  position: absolute;
  top: 10%;
  left: 80%;
  font-weight: bold;
  font-size: 1.8rem;
  display: none;
  animation: slidein 2s;
  color: aliceblue;
}

@keyframes slidein {
  0% { margin-left:-20vw; }
  100% { margin-left:0vw; }
}

.totalScore{
  width: 15vw;
  height: 5vh;
  position: absolute;
  top: 10%;
  left: 80%;
  font-weight: bold;
  font-size: 1.8rem;
  display: none;
  animation: slidein 2s;
  color: aliceblue;
}

.level{
  width: 15vw;
  height: 5vh;
  position: absolute;
  top: 10%;
  left: 10%;
  font-weight: bold;
  font-size: 1.8rem;
  color: aliceblue;
  opacity: 0;
}

